*{
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
}

ul{
    margin-bottom: 0;
    display: flex;
    align-items: center;
}

nav{
    display: flex;
    align-items: center;
    justify-content: space-around;
    font-family: 'Poppins', sans-serif;
    background-image: -webkit-gradient(linear, left top, right top, from(#43e97b), to(#38f9d7));
    background-image: linear-gradient(to right, #43e97b 0%, #38f9d7 100%);
    min-height: 8vh;
    text-align: center;
}

.logo{
    color:white;
    text-transform: uppercase;
    letter-spacing: 5px;
    font-size: 25px;
    text-align: center;
}

.logo h4{
    margin: 0px;
    padding: 0px;
}

.nav-links{
    display:flex;
    justify-content: space-around;
    width: 50%;
    z-index: 2;

}

.nav-links li{
    list-style: none;
}

.nav-links a{
    text-decoration: none; 
    color: white;
    letter-spacing: 3px;
    font-weight: bold;
    font-size: 14px;
}

.burger{
    display: none;
    cursor: pointer;
}

.burger div{
    width: 25px;
    height: 5px;
    background-color: white;
    margin: 5px;
    -webkit-transition: all 0.5s ease;
    transition: all 0.5s ease;
}

@media screen and (max-width: 1024px){
    .nav-links{
        width: 60%;
    }
}

@media screen and (max-width: 768px){
    body{
        overflow: hidden;
    }

    .nav-links{
        overflow: hidden;
        position: fixed;
        right: 0;
        height: 92vh;
        top: 8vh;
        background-color: blue;
        width: 50%;
        display: flex;
        flex-direction: column;
        align-items: center;
        color: white;
        -webkit-transform: translateX(100%);
                transform: translateX(100%);
        -webkit-transition: -webkit-transform 0.5s ease-in;
        transition: -webkit-transform 0.5s ease-in;
        transition: transform 0.5s ease-in;
        transition: transform 0.5s ease-in, -webkit-transform 0.5s ease-in;
    }

    .nav-links li{
        opacity: 0;
    }

    .burger{
        display: block;
    }
}

.nav-active{
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
}

@-webkit-keyframes navLinkFade{
    from{
        opacity: 0;
        -webkit-transform: translateX(50px);
                transform: translateX(50px);
    }
    to{
        opacity: 1;
        -webkit-transform: translateX(0px);
                transform: translateX(0px);
    }
}

@keyframes navLinkFade{
    from{
        opacity: 0;
        -webkit-transform: translateX(50px);
                transform: translateX(50px);
    }
    to{
        opacity: 1;
        -webkit-transform: translateX(0px);
                transform: translateX(0px);
    }
}

.toggle .line1{
    -webkit-transform: rotate(-45deg) translate(-8px,6px);
            transform: rotate(-45deg) translate(-8px,6px);
}
.toggle .line2{
    opacity: 0;
}
.toggle .line3{
    -webkit-transform: rotate(45deg) translate(-8px,-6px);
            transform: rotate(45deg) translate(-8px,-6px);
}
.Tools{
    display: flex;
    background-color:white;
    border-radius: 10px;
    box-shadow: 11px 16px 28px -8px rgba(0,0,0,0.37);
    margin: auto;
    align-items: center;
    width: 90%;
}
.Tools li{
    list-style: none;
    text-align: center;
    display: block;
    flex: 1 1;
    /* margin-left: auto; */
    /* margin-right: auto; */

}

.Tools li a{
    text-decoration: none;
    display: flex;
    flex-direction: column;
    /* padding: 5px 50px; */
    padding: 0 auto;
    margin: 0 auto;
    max-width: 100%;
}
.Tools li{
    border-right: 1px solid rgba(0,0,0,0.2);
}

.Tools li:last-child{
    border-right: none;
}

.Tools li a .icon{
    width: 1.5em;
    height: 1.5em;
    /* background-color: gray; */
    text-align: center;
    margin: 0 auto 10px;
}

.Tools li a .icon .fa{
    width: 100%;
    height: 100%;
    line-height: 40px;
    font-size: 34px;
    -webkit-transition: 0.5s;
    transition: 0.5s;
}

.Tools li a .icon .fa:first-child{
    overflow: hidden;
}

.Tools li a:hover .icon .fa:last-child{
    -webkit-transform: translateY(-20%);
            transform: translateY(-20%);
}

